html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--dark-bg);
  color: var(--dark-text2);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightRed: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-start: hsl(210, 78%, 56%);
  --toggle-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(229, 27%, 20%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

.container {
  max-width: 69.375rem;
  padding: 0 1.5625rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.card {
  padding: 1.25rem;
}

.header {
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--dark-text1);
}

.toggle {
  border: none;
}
@media (max-width: 39.9375em) {
  .toggle {
    justify-content: end;
    display: flex;
  }
}
.toggle__wrapper {
  display: inline-block;
  position: relative;
  height: 1.5rem;
  width: 4rem;
  vertical-align: middle;
}
.toggle input[type=radio] {
  position: relative;
  vertical-align: middle;
  width: 0.5625rem;
  margin: 0rem;
  margin-left: 0.1875rem;
  transform: scale(1.7);
  opacity: 0;
  z-index: 1;
  cursor: pointer;
}
.toggle input[type=radio]:hover {
  cursor: pointer;
}
.toggle__background {
  display: block;
  position: absolute;
  border-radius: 0.75rem;
  overflow: hidden;
  background: linear-gradient(225deg, var(--toggle-end) 0%, var(--toggle-start) 98.02%);
  z-index: 0;
  top: 0;
  left: 0;
  width: 75%;
  height: 100%;
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  top: 0.125rem;
  left: 0.1875rem;
  width: 1rem;
  height: 1.25rem;
  border-radius: 50%;
  background-color: var(--dark-bg);
  transition: all 1s;
}
#dark:checked ~ .toggle__button {
  left: 5%;
  right: 0%;
}
#system:checked ~ .toggle__button {
  left: 24%;
  right: 0%;
}
#light:checked ~ .toggle__button {
  left: 45%;
  right: 0%;
}
.toggle__switch:last-child {
  margin-left: -0.5rem;
}
.toggle__switch:first-child {
  padding-right: 0.625rem;
}
.toggle label[for=system] {
  display: block;
  position: relative;
  top: 0.75rem;
  left: 0.3125rem;
}

.main__top-container {
  margin: 0;
  padding: 0;
  display: flex;
  gap: 2.5rem;
  align-items: center;
  justify-content: center;
}
.main__line {
  margin: 0;
  padding: 0;
  position: relative;
  top: -1.5rem;
  width: 100%;
  height: 0.3125rem;
  border-radius: 0.1875rem 0.1875rem 0 0;
}
.main__stats {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 15.9375rem;
  height: 13.5rem;
  background: var(--dark-card);
  border-radius: 0.1875rem 0.1875rem 0.3125rem 0.3125rem;
}
.main__stats:hover {
  background: rgb(47, 54, 84);
  cursor: pointer;
}
.main__icon {
  margin: 0;
  padding: 0;
  display: inline-block;
}
.main__acc {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 0 1.875rem;
  font-size: 0.75rem;
  color: var(--dark-text1);
}
.main__img {
  margin-right: 0.5rem;
}
.main__gain {
  font-size: 3.125rem;
  font-weight: 600;
  color: var(--dark-text2);
}
.main__following {
  letter-spacing: 0.3125rem;
  font-size: 0.875rem;
  color: var(--dark-text1);
}
.main__today {
  padding-top: 1.25rem;
  font-size: 0.75rem;
}

.overview {
  display: block;
}
.overview__bottom-container {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 1.875rem;
  overflow: hidden;
}
.overview__card {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  min-width: 15.9375rem;
  height: 7.8125rem;
  background: var(--dark-card);
  border-radius: 0.3125rem;
}
.overview__card:hover {
  background: rgb(47, 54, 84);
  cursor: pointer;
}
.overview__topic {
  display: flex;
  justify-content: space-around;
  width: 100%;
  font-size: 0.875rem;
  color: var(--dark-text1);
}
.overview__stat {
  display: flex;
  justify-content: space-around;
  width: 100%;
  font-size: 2.1875rem;
  font-weight: 600;
}
.overview__percent {
  display: flex;
  align-items: end;
  padding-bottom: 0.3125rem;
  font-size: 0.75rem;
}